// Video container styling
.video-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  border-radius: 12px;
  box-shadow: 0 8px 24px rgba(15, 23, 42, 0.08);
  margin-bottom: 20px;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;

  &:hover {
    transform: translateY(-4px);
    box-shadow: 0 12px 32px rgba(15, 23, 42, 0.12);
    
    .video-play-button {
      transform: translate(-50%, -50%) scale(1.1);
    }
  }

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: none;
  }
}

// Video thumbnail styling
.video-thumbnail {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

// Play button overlay
.video-play-button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: none;
  border: none;
  cursor: pointer;
  opacity: 0.9;
  transition: all 0.3s ease;
  z-index: 10;
  
  &:hover {
    opacity: 1;
  }
  
  svg {
    filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.3));
  }
}

// Video title styling
.video-title {
  font-weight: 600;
  font-size: 1.05rem;
  margin: 0 0 8px 0;
  line-height: 1.4;
}

// Video description styling
.video-description {
  font-size: 0.95rem;
  line-height: 1.6;
  color: var(--text-light);
  margin: 0;
}
